import { curveCatmullRom } from 'd3-shape'
import '../../node_modules/react-vis/dist/style.css'
import {
  XYPlot,
  VerticalBarSeries,
  VerticalGridLines,
  HorizontalGridLines
} from 'react-vis'
import _ from 'lodash'

export default class Chart extends React.Component {
  render () {
    // const data = [{ x: 0, y: 8 }, { x: 1, y: 5 }, { x: 2, y: 3 }]

    let d = _.groupBy(this.props.data.contracts, 'currentStatus.name')
    console.log(d)
    let data = _.values(d).map((value, index) => {
      return {
        x: index,
        y: value.length,
        color: value[0].currentStatus.color,
        opacity: 0.7
      }
    })
    console.log(data)

    return (
      <div className='pa3'>
        <XYPlot xType='ordinal' height={400} width={400} colorType='literal'>
          <VerticalBarSeries data={data} />
        </XYPlot>

      </div>
    )
  }
}



